[class^="col"] {
  padding: 0;
}

.row {
  padding: 0;
  margin: 0;
}

.container-fluid {
  padding: 0;
  margin: 0;
  overflow: hidden;
  height: 100vh;
}

#app-navbar-collapse {
  padding: 0;
}

.menu-raq {
  min-height: 100%;
  min-height: 100vh;
  background-color: #3E505C;
}

#time {
  font-size: 3em;
  margin: 10px 30px;
}

#time.start {
  color: #17AEF3;
}

.name {
  font-size: 1.5em;
  color: #fff;
  line-height: 71px;
}

.circle {
  display: block;
  margin: 15px;
  width: 40px;
  height: 40px;
  background: #fff;
  border-radius: 50%;
}

.nav > li > a:hover, .nav > li > a:focus {
  text-decoration: none;
  background-color: transparent;
}

.header-raq {
  height: 75px;
}

.dropdown-menu {
  background: #3E505C;
}

.panel-body {
  padding: 0;
}

a:focus, a:active {
  background: transparent;
}

.nav .open a:focus {
  background: transparent;
}

.panel-heading {
  font-size: 1.7em;
  margin-bottom: 10px;
}

#what_action {
  width: 67%;
  margin: 15px;
  height: 85px;
  margin-bottom: 70px;
  margin-left: 50px;
}

.colunm0 {
  width: 2%;
}

.error-block {
  margin: 10% auto;
  width: 61%;
  font-size: 24px;
  text-align: center;
}

.error-icon {
  font-size: 2.4em;
}

.timer {
  margin-top: 30px;
  margin-bottom: -10px;
  margin-left: 30px;
}

.material-icons {
  margin-right: 7px;
}

.navbar-raq {
  width: 100%;
}

.navbar-raq li a {
  border-left: 3px solid transparent;
}

.navbar-raq li.active a {
  border-left: 3px solid #EB6555;
  background: #4e5e69;
}

.navbar-raq li a {
  width: 100%;
  color: #fff;
}

.navbar-raq li a:hover {
  color: #3097D1;
}

table tbody {
  border-bottom: 1px solid #ddd;
}

table tr > th, table tr > td {
  padding-left: 25px !important;
}

table tr > td {
  vertical-align: middle !important;
  height: 58px;
}

.btn-for-time {
  display: inline-block;
  position: relative;
  top: -9px;
}

.title-task {
  margin-left: 15px;
}

.box-task {
  padding: 10px;
  background: #F8F8F8;
  margin-top: -30px;
  margin-left: 25px;
}

.box-description {
  margin-right: -15px;
  padding-left: 32px;
}

.noUi-base {
  background: #1EB0F2;
}

.noUi-handle {
  background: #1EB0F2 !important;
  border-color: #1EB0F2 !important;
}

.box-info {
  padding: 0;
}

.box-info li:not(:last-child) {
  border-bottom: 1px solid #ddd;
}

.box-info li {
  list-style: none;
  padding-top: 7px;
  padding-bottom: 7px;
}

.high {
  color: #e67676;
}

.meddium {
  color: #dce464;
}

a, a:focus, a:hover {
  color: #8eb4cb;
}

.low {
  color: #64cde4;
}

.colunm2,
.colunm3,
.colunm4 {
  width: 10%;
}

.panel-body {
  max-height: 100vh;
  min-height: calc(100vh - 60px);
  overflow: auto;
}

.form-group {
  margin-top: 0;
}

.text-align-center {
  text-align: center;
}

.text-align-center .btn {
  display: inline-block;
}
